@import "swiper.css";
@import "icon.css";
@keyframes downShow {
  from {
    opacity: 0;
    transform: translateY(-5px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}
@keyframes loading {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(180deg);
  }
}
@keyframes toTopShow {
  from {
    opacity: 0;
    transform: translateY(0px);
  }
  to {
    opacity: 1;
    transform: translateY(-10px);
  }
}
.clearfix:after {
  content: '';
  position: relative;
  width: 0;
  height: 0;
  display: block;
  clear: both;
}
